// Enhanced mock data with localStorage persistence
const STORAGE_KEY = "white-market-gigs";

// Load gigs from localStorage or use default mock data
const loadGigsFromStorage = () => {
  if (typeof window !== "undefined") {
    const stored = localStorage.getItem(STORAGE_KEY);
    if (stored) {
      try {
        const parsed = JSON.parse(stored);
        // Merge with default mock data if stored data exists
        return [...mockGigs, ...parsed];
      } catch (e) {
        console.error("Error parsing stored gigs:", e);
      }
    }
  }
  return [...mockGigs];
};

// Save gigs to localStorage
const saveGigsToStorage = (gigs: any[]) => {
  if (typeof window !== "undefined") {
    // Only save newly created gigs (not the default mock data)
    const newGigs = gigs.filter(
      (gig) => !mockGigs.find((defaultGig) => defaultGig.id === gig.id),
    );
    localStorage.setItem(STORAGE_KEY, JSON.stringify(newGigs));
  }
};

