// New API functions for enhanced functionality
export const getReviews = async (gigId: string) => {
  return mockReviews.filter((review) => review.gigId === gigId);
};

export const createReview = async (review: any) => {
  const newReview = {
    ...review,
    id: Date.now().toString(),
    date: new Date().toISOString().split("T")[0],
  };
  mockReviews.push(newReview);
  return newReview;
};

export const getNotifications = async (userId: string) => {
  return mockNotifications.filter(
    (notification) => notification.userId === userId,
  );
};

export const markNotificationAsRead = async (notificationId: string) => {
  const notification = mockNotifications.find((n) => n.id === notificationId);
  if (notification) {
    notification.read = true;
  }
  return notification;
};

export const getFavorites = async (userId: string) => {
  return mockFavorites.filter((fav) => fav.userId === userId);
};

export const addToFavorites = async (userId: string, gigId: string) => {
  const existing = mockFavorites.find(
    (fav) => fav.userId === userId && fav.gigId === gigId,
  );
  if (!existing) {
    const newFavorite = {
      id: Date.now().toString(),
      userId,
      gigId,
      createdAt: new Date().toISOString(),
    };
    mockFavorites.push(newFavorite);
    return newFavorite;
  }
  return existing;
};

export const removeFromFavorites = async (userId: string, gigId: string) => {
  const index = mockFavorites.findIndex(
    (fav) => fav.userId === userId && fav.gigId === gigId,
  );
  if (index > -1) {
    mockFavorites.splice(index, 1);
    return true;
  }
  return false;
};

export const searchGigs = async (query: string, filters: any = {}) => {
  let filteredGigs = mockGigsData.map((gig) => {
    const mockUser = mockUsers.find((user) => user.id === gig.userId);
    return {
      ...gig,
      userName: mockUser?.name || gig.userName,
      userAvatar: mockUser?.avatar || gig.userAvatar,
      userId: gig.userId,
    };
  });

  // Text search
  if (query) {
    filteredGigs = filteredGigs.filter(
      (gig) =>
        gig.title.toLowerCase().includes(query.toLowerCase()) ||
        gig.description.toLowerCase().includes(query.toLowerCase()) ||
        gig.category.toLowerCase().includes(query.toLowerCase()),
    );
  }

  // Category filter
  if (filters.category) {
    filteredGigs = filteredGigs.filter(
      (gig) => gig.category === filters.category,
    );
  }

  // Price range filter
  if (filters.minPrice) {
    filteredGigs = filteredGigs.filter((gig) => gig.price >= filters.minPrice);
  }
  if (filters.maxPrice) {
    filteredGigs = filteredGigs.filter((gig) => gig.price <= filters.maxPrice);
  }

  // Rating filter
  if (filters.minRating) {
    filteredGigs = filteredGigs.filter(
      (gig) => gig.rating >= filters.minRating,
    );
  }

  // Sort
  if (filters.sortBy) {
    switch (filters.sortBy) {
      case "price_low":
        filteredGigs.sort((a, b) => a.price - b.price);
        break;
      case "price_high":
        filteredGigs.sort((a, b) => b.price - a.price);
        break;
      case "rating":
        filteredGigs.sort((a, b) => b.rating - a.rating);
        break;
      case "newest":
        // Since mock data doesn't have createdAt, sort by ID (newer IDs = newer gigs)
        filteredGigs.sort((a, b) => parseInt(b.id) - parseInt(a.id));
        break;
      default:
        break;
    }
  }

  return filteredGigs;
};

export const getCategories = async () => {
  const categories = [...new Set(mockGigsData.map((gig) => gig.category))];
  return categories.map((category) => ({
    name: category,
    count: mockGigsData.filter((gig) => gig.category === category).length,
  }));
};

export const getUserStats = async (userId: string) => {
  const userGigs = mockGigsData.filter((gig) => gig.userId === userId);
  const userOrders = await getOrders(userId);

  return {
    totalGigs: userGigs.length,
    totalOrders: userOrders.length,
    completedOrders: userOrders.filter((order) => order.status === "completed")
      .length,
    totalEarnings: userOrders
      .filter(
        (order) => order.seller_id === userId && order.status === "completed",
      )
      .reduce((sum, order) => sum + order.amount, 0),
    averageRating:
      userGigs.length > 0
        ? userGigs.reduce((sum, gig) => sum + gig.rating, 0) / userGigs.length
        : 0,
  };
};

