export const createGig = async (gigData: any) => {
  const newGig = {
    ...gigData,
    id: Date.now().toString(),
    createdAt: new Date().toISOString(),
    rating: 0,
    reviews: 0,
  };
  mockGigsData.push(newGig);
  saveGigsToStorage(mockGigsData);
  return newGig;
};

export const updateGig = async (gigId: string, updates: any) => {
  const gigIndex = mockGigsData.findIndex((gig) => gig.id === gigId);
  if (gigIndex > -1) {
    mockGigsData[gigIndex] = { ...mockGigsData[gigIndex], ...updates };
    saveGigsToStorage(mockGigsData);
    return mockGigsData[gigIndex];
  }
  throw new Error("Gig not found");
};
